<template>
    <div class="d-flex justify-space-between">
        <div class="d-flex justify-center align-center" id="conteudo" style="background-color: #114B5F; height: 62rem; width: 45%">
          <v-img data-aos="zoom-in" src="./images/logobranco.png" max-width="70%"></v-img>
        </div>
        <div class="d-flex justify-center align-self-center" style="width: 95%; height: 100%;">
          <!-- <v-card class="d-flex flex-column justify-start align-center" style="background-color: #BCEEFF; width: 40%; height: 55%;"></v-card> -->
            <v-card data-aos="zoom-in-up" max-width="500" class="mx-auto pa-4" style="background-color: #BCEEFF; width: 40%; height: 55%;">
                <v-card-title class=" justify-space-between">
                    <span class="text-h6 font-weight-bold text-decoration-underline" style="color:#114B5F;">{{ currentTitle }}</span>
                    <v-avatar color="#114B5F" class="subheading white--text" size="24" v-text="step" style=""></v-avatar>
                </v-card-title>
                
                <v-window v-model="step">

                <v-window-item :value="1">
                    <div>
                        <v-form ref="form" lazy-validation class="ma-5"> 
                            <v-text-field color="#114B5F" prepend-inner-icon="mdi-email" v-model="email" label="E-mail" required outlined></v-text-field> 
                            <v-text-field color="#114B5F" prepend-inner-icon="mdi-account" v-model="nome" :counter="100" label="Nome" required outlined style=""></v-text-field>
                            <v-menu ref="menu" v-model="menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" offset-y min-width="auto" outlined >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-text-field v-model="date" label="Data de aniversário" prepend-inner-icon="mdi-cake-variant-outline" readonly v-bind="attrs" v-on="on" color="#114B5F" outlined ></v-text-field>
                                </template>
                                <v-date-picker v-model="date" no-title scrollable>
                                    <v-spacer></v-spacer>
                                    <v-btn text color="#114B5F" @click="menu = false" > Cancel </v-btn>
                                    <v-btn text color="#114B5F" @click="$refs.menu.save(date)" > OK </v-btn>
                                </v-date-picker>
                            </v-menu>
                            <v-text-field class="mt-0" color="#114B5F" required outlined prepend-inner-icon="mdi-lock" label="Senha" v-model="password" :type="show ? 'text' : 'password'" :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" @click:append="show = !show" ></v-text-field> 
                            <v-text-field class="mt-0" color="#114B5F" required outlined prepend-inner-icon="mdi-lock-alert" label="Confirmar senha" v-model="passwordConfirm" :type="show ? 'text' : 'password'" :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" @click:append="show = !show" ></v-text-field> 
                        </v-form>
                    </div>
                    
                </v-window-item>

                <v-window-item :value="2">
                    <h3 class="ma-3 text-center font-weight-light" style="color:#114B5F">Sua conta foi criada, <b class="font-weight-bold">em breve você receberá um e-mail confirmando seu cadastro.</b> </h3>
                    <div class="d-flex justify-center">
                        <svg  width="120" height="120" viewBox="0 0 209 217" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                            <rect width="209" height="217" fill="url(#pattern0)"/>
                            <defs>
                            <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlink:href="#image0_487_91" transform="translate(0 0.0184332) scale(0.002 0.00192627)"/>
                            </pattern>
                            <image id="image0_487_91" width="500" height="500" xlink:href="data:image/png;base64,"/>
                            </defs>
                        </svg>                        
                    </div>
                    <div class="mt-4 d-flex justify-center">
                        <v-btn link to="/" style="color:#114B5F">
                                Inicio
                        </v-btn>                        
                    </div>

                </v-window-item>
                </v-window>

                <v-divider class="mt-16"></v-divider>

                <div class="d-flex justify-space-around align-end mt-5">
                        <v-btn :disabled="step === 1 || step === 2" text @click="step--" >
                            Voltar
                        </v-btn>
                        <v-btn :disabled="step === 2" color="#114B5F" style="color: #FFF;" depressed @click="step++" >
                            Continuar
                        </v-btn>                  
                </div>
            </v-card>
        </div>        
    </div>
</template>

<script>
  export default {
    data(){
        return{
            nome: '',
            date: '',
            menu: '',
            email: '',
            password: '',
            passwordConfirm: '',
            step: 1,
            show: false,
        }
    },
    computed: {
      currentTitle () {
        switch (this.step) {
          case 1: return 'Cadastro'
          default: return 'Sucesso!'
        }
      },
    },
  }
</script>

<style>
  /* STYLE DEFAULT */
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;700;800&display=swap');
  
  ::-webkit-scrollbar{
    display: none;
  }
  * {
      padding: 0;
      margin: 0;
      font-family: 'Inter', sans-serif;
  }
  /* ............. */
</style>